import React from 'react';
import { Link } from 'gatsby';
import classes from './Item.module.scss';
import { Rate } from 'antd';
function Item({ slug, price, title, img, rate }) {
	return (
		<Link className={classes.linktext} to={slug}>
			<div>
				<div className="d-flex justify-content-center">
					<img className={classes.img} alt="img" src={img} />
				</div>
				<p className={classes.title}>{title}</p>
				<div className="d-flex justify-content-center">
					<Rate style={{ color: 'black' }} allowHalf defaultValue={rate} disabled />
				</div>
				<p className={classes.price}>{price.toLocaleString('ar-EG')} هزارتومان</p>
				<div className="d-flex justify-content-center mt-2">
					<button className={classes.btn}>خرید</button>
				</div>
			</div>
		</Link>
	);
}

export default Item;
