import React, { Fragment } from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import Media from 'react-media';
import Slider from 'react-slick';

// Components
import BuyCollection from '../BuyCollection/BuyCollection';
import Item from '../Item/Item';

import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';

function Collection() {
	const data = useStaticQuery(graphql`
		{
			allContentfulProduct {
				edges {
					node {
						price
						slug
						img {
							file {
								url
							}
						}
						titlemain
						descmain
						rate
					}
				}
			}
		}
	`);
	const settings = {
		dots: false,
		infinite: true,
		speed: 500,
		slidesToShow: 3,
		slidesToScroll: 3
	};
	const settingssmall = {
		dots: false,
		infinite: true,
		speed: 500,
		slidesToShow: 1,
		slidesToScroll: 1
	};

	return (
		<div className="pt-4 pb-4 pl-5 pr-5">
			<div className="row">
				<div className="col-lg-6 col-md-6 col-sm-12 col-12 d-flex flex-column align-items-center justify-content-center">
					<BuyCollection />
				</div>
				<div className="col-lg-6 col-md-6 col-sm-12 col-12">
					<Media
						queries={{
							small: '(max-width: 850px)',
							large: '(min-width: 850px)'
						}}
					>
						{(matches) => (
							<Fragment>
								{matches.small && (
									<Slider className="mt-3 pb-3 mr-3 ml-3" {...settingssmall}>
										{data.allContentfulProduct.edges.map((each) => {
											return (
												<Item
													key={each.node.slug}
													slug={each.node.slug}
													img={each.node.img[0].file.url}
													title={each.node.titlemain}
													price={each.node.price}
													rate={each.node.rate}
												/>
											);
										})}
									</Slider>
								)}

								{matches.large && (
									<Slider className="mt-3 pb-3" {...settings}>
										{data.allContentfulProduct.edges.map((each) => {
											return (
												<Item
													key={each.node.slug}
													slug={each.node.slug}
													img={each.node.img[0].file.url}
													title={each.node.titlemain}
													price={each.node.price}
													rate={each.node.rate}
												/>
											);
										})}
									</Slider>
								)}
							</Fragment>
						)}
					</Media>
				</div>
			</div>
		</div>
	);
}

export default Collection;
