import React from 'react';

// Components
import Layout from '../components/Layout/Layout';
import Background from '../components/Background/Background';
import BottomBackground from '../components/BottomBackground/BottomBackground';
import Collection from '../components/Collection/Collection';
import Insta from '../components/Insta/Insta';
import { graphql, useStaticQuery } from 'gatsby';

export default function Home() {
	const insta = useStaticQuery(graphql`
		{
			allInstaNode(limit: 6, sort: { order: DESC, fields: timestamp }) {
				edges {
					node {
						likes
						id
						thumbnails {
							src
						}
					}
				}
			}
		}
	`);

	return (
		<Layout title="صفحه اصلی">
			<Background />
			<Collection />
			<BottomBackground />
			<Insta insta={insta.allInstaNode.edges} />
		</Layout>
	);
}
