import React from 'react';
import classes from './Background.module.scss';

function Background() {
	return (
		<div className={classes.back}>
			<div className={classes.text}>
				<p className={classes.date}>پنجم آبان امسال بعدازظهر</p>
				<p className={classes.title}>کریستال</p>
				<p className={classes.desc}>
					کالکشن کریستال به کمک برند بزودی لانچ خواهد شد و منتظر نظرات مشتریامون هستیم
				</p>
				<div className="d-flex justify-content-end">
					<button className={classes.btn}>پیش خرید</button>
				</div>
			</div>
		</div>
	);
}

export default Background;
