import React from 'react';
import classes from './Insta.module.scss';
import like from '../../img/like.png';
import { Link } from 'gatsby';
function Insta({ insta }) {
	return (
		<div className={`container ${classes.cont}`}>
			<p className={classes.flw}>اینستاگرام مارو فالو کنید</p>
			<p className={classes.flwlink}>@KKWBEAUTY - #KKWBEAUTY</p>
			<div className="row mr-1 ml-1">
				{insta.map((each) => {
					const image = each.node.thumbnails[3].src;
					const likes = each.node.likes;
					const id = each.node.id;
					const url = `https://www.instagram.com/p/${id}`;
					return (
						<div key={id} className="col-4 p-0">
							<Link className={classes.link} to={url} target="_blank">
								<img className={`img-fluid ${classes.img}`} alt="insta" src={image} />
								<div className={classes.likes}>
									<img className={classes.heart} alt="likes" src={like} />
									<p className={classes.number}>{likes.toLocaleString('ar-EG')}</p>
								</div>
							</Link>
						</div>
					);
				})}
			</div>
		</div>
	);
}

export default Insta;
