import React from 'react';
import classes from './BottomBackground.module.scss';

function BottomBackground() {
	return (
		<div className={classes.back}>
			<div className={classes.cont}>
				<p className={classes.text}>محصولات روزمره</p>
				<p className={classes.title}>روزمره های کیم کاردشیان</p>
				<p className={classes.text}>محصولات کلاسیک روزمره کیم رو خریداری کنید</p>
				<div className="d-flex justify-content-end">
					<button className={classes.btn}>خرید</button>
				</div>
			</div>
		</div>
	);
}

export default BottomBackground;
