import React, { Fragment } from 'react';
import classes from './BuyCollection.module.scss';

function BuyCollection() {
	return (
		<Fragment>
			<p className={classes.title}>کالکشن بهاره برای تمام سنین</p>
			<div className="d-flex justify-content-center">
				<button className={classes.btn}>خرید کالکشن</button>
			</div>
		</Fragment>
	);
}

export default BuyCollection;
